.home {
    height        : 100%;
  width: fit-content;
  justify-content: center;
  align-items: center;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    display: flex;
}

#home_main {
    flex-direction: row;
    display       : flex;
    width: 100%;
    justify-content: center;
}

.left {
    width: 5%;
}

.content {
    width : 90%;
    height: 100%;
}

.right {
    width : 5%;
}

.text_main {
    display        : flex;
    margin-left    : auto;
    font-weight: normal;
    text-align: center;
    width: 80%;
    font-size: 22px;
    margin-right   : auto;
    justify-content: center;
}

.text_main_title {
    font-size: 46px;

}

.r_image {
    margin-right: auto;
    width      : 400px;
    border-radius: 28px;
    border:1px solid black;
    height:600px;

}

.p_image {
    width      : 400px;
    border:1px solid black;
    height:600px;
    margin-left: auto;
    min-width: 400px;
    border-radius: 28px;

}

.box_container {
    display       : flex;
    flex-direction: row;

    
    width:100%;
}