
.form_title {
    font-size  : 54px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: bold;
}


.form_container {
    display        : flex;
    flex-direction : column;
    justify-content: center;
    padding        : 16px;
    height:50%;
    width          : 50%;
    color: white;
    height: fit-content;
    max-width:720px;
    min-width: 400px;

}

.welcome_text {
    text-align: start;
}
.textBox {
    padding      : 14px;
    font-size    : 14px;
    background-color: aliceblue;
    outline-color: rgb(202, 16, 16);
    outline-width: .1px;
    width        : 100%;
    font-size: 22px;
    margin-bottom: 32px;
    border       : none;
    text-align   : start;
}

.form_bottom {
    display: flex;
    width: 100%;
    align-content: baseline;
 }

 
.side-button {
    margin-left: auto; 
    color: blueviolet; 
    background: none;
    font-weight: normal; 
    border: none;
    text-decoration: underline;
}

.form_submit {
    display: flex;
     align-self: flex-start;
     font-size: 24px;
     border: 1px solid white;
     border-radius: 8%;
     background-color: crimson;
     color: white;
     padding:16px;
 }
 
 .form_submit:hover {
    color:aliceblue;
}

label {
    font-weight: bold;
    padding:4px;
    align-self: flex-start;
    margin:0px;
    align-content: center;
    display: flex;
}

