/**
    Courses Component
**/
.courses_page_side {
display: flex;

height:fit-content;
width:10%;
flex-direction: column;
align-items: center;
justify-content: center;

color:white;
background-color: rgb(194, 193, 193);
}
#courses_page {

}
#all_courses_btn {
    color: white;
    font-weight: bold;
    margin-left: 64px;
    background: none;
    border:none;
    
    align-self: flex-start;
}
#search {
        align-self: flex-start;
    border:none;
    margin-right: 32px;
    padding: 10px;
    background: url("https://static.thenounproject.com/png/101791-200.png") no-repeat  left rgb(238, 238, 238);
    background-size: 20px;
}
#area-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
}
#title_courses {
    align-self: flex-start;
    margin-left: 50px; 
    color:white;
}
#course_header {
    color:white;
    
}



.courses_area {
    column-gap: 16px;
    padding: 32px;
    row-gap: 32px;
    margin-right: auto;
    margin-left: auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
}

@media only screen and  (max-width:1082px) {
    .courses_area {
        grid-template-columns: 1fr 1fr ;
    }
    #search {
        margin-right: 48px;
    }

    #course_page {

        overflow-x:scroll;
         }
}

@media only screen and  (max-width:800px) {
    #search {
        margin-right: 48px;
    }

}


/**
    CourseCard Component
**/
.course_card_image {
width:320px;
}
.course_card_name,.course_card_price {
  text-align: center;
}
.course_card {
    top:10px;
    width:320px;
    border-radius: 8px;
    height: fit-content;
    position: relative;
    overflow: hidden;
    background-color: 	rgb(244, 244, 244);
}
.course_card.h3 {
    
    width:100px;
    height:100px;
}

/**
used for 'course section'
**/
.current_section {
align-self: flex-start;
text-align: start;
}

.sections_tab {
border:1px solid orange;
display: flex;
color: rgb(67, 59, 59);
height: 100%;
background-color: antiquewhite;
align-content: center;
align-self: flex-start;
text-align: center;
flex-direction: column;
width:15%;
margin:8px;
min-width: 220px;
max-width: 200px;
}

#section_list {
    width:15%;
    border:1px solid red;
}
#section_content {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    flex-direction: column;
    
    
}
#c_desc {

}
#c_title {


}
/*

Course 
*/

.course_holder {

    display: flex;
    width: 100vw;
    height:100%;
    flex-direction: row;
}

.section_holder {
  display: flex;
  flex-direction: column;  

  min-width: 100px;
}
#section_video {
    width: 1089;
    border:1px solid red;
    min-width: 100px;
    flex-direction: column;
}
#section_wrapper {
    flex-direction: row;
    display: flex;

    text-align: left;
    min-width: 400px;
    
}
#video_holder {
    display: flex;
    min-width: 100px;
}


li {
    padding-top:8px;
    padding-bottom:8px;
    text-decoration: underline;
    text-align: start;
    cursor: pointer;
}

li:hover {
    font-weight: bold;
}
@media only screen and (max-width:900px) {
    .course_holder {
      flex-direction: column;
        text-align: center;
      align-items: flex-start;
      justify-content: center;
     
      margin: 0px;
    }
    #section_wrapper {
        flex-direction: column;
        align-items: flex-start;
        text-align: center;
        margin: 0px;
        
        justify-content: center;
    } 
    h3,h4 {
        text-align: start;
    
    }
    #video_holder {
        width: 80%;
    }
    #courses_page {
        height: fit-content;
    }
    .sections_tab {
       position: relative;
    }
    h1 {
        text-align: start;
    }
}