.App {
  text-align: center;
  padding: 0px;
  height: 100%;
}

.App-logo {
  height        : 40vmin;
  pointer-events: none;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {

  flex-direction: column;
  width         : 100%;

  font-size     : calc(10px + 2vmin);
  color         : white;
  position: sticky;
  top : 0;
  z-index: 9;

}
 
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}